export default [
  {
    path: '/error-404',
    name: 'error-404',
    component: () => import('@/views/error/Error404.vue'),
    meta: {
      layout: 'full',
      resource: 'Auth',
      action: 'read',
    },
  },
  {
    path: '/no-autorizado',
    name: 'no-autorizado',
    component: () => import('@/views/error/NoAutorizado.vue'),
    meta: {
      requiresAuth: true,
      layout: 'full',
      resource: 'Auth',
      action: 'read',
    },
  },
]
