    //     .then(res => (res.data.autentificacion === 1 ? next() : next({ name: 'vista-anonima-x', params: { id_formulariox: to.params.id_formulariox } })))
    //     .catch(() => {
    //       next({ name: 'listar-formularios-index' })
    //     })
    // },
